import { IndexModel } from "../../model/index";
const indexModel = new IndexModel();
export default {
    name: "collectCars_record",
    data() {
        return {
            recordList: [],
            recordData: {
                page: 1,
                size: 10
            }
            
        }
    },
    methods: {
        // 提交记录
        getSubRecord() {
            indexModel.subRecord(this.recordData).then(res => {
                if(res.code !== 200) {
                    uni.showToast({
                        title: '获取失败，请稍后重试',
                        icon: 'none'
                    });
                }
                if(res.code == 200){
                    let a = this.recordList.concat(res.data)
                    this.recordList = a;
                    if(res.data.length==0){
                    }else{
                        this.recordData={page:this.recordData.page+1,size:1}
                    }
                    console.log(this.recordList);
                    this.recovery_log_id = res.data.id;
                }
            });
        },
        // 催单
        reminder(id) {
            console.log(id);
            let recovery_log_id = id;
            console.log(recovery_log_id);
            indexModel.reminder({recovery_log_id:recovery_log_id}).then( (res) => {
                console.log(res);
                if((res.code == 201) || (res.code == 200)) {
                    uni.showToast({
                    title: res.message,
                    icon: 'none'
                });
                }
               
            } );
        }
    },
    onLoad() {
        this.getSubRecord();
    },
    onReachBottom() {
        this.getSubRecord();
        console.log(123);
    }
}
